#menupanel {
  background-color: black;
  color: white;
  -ms-flex-line-pack: center;
      align-content: center;
  height: 100%;

  padding: 0;
  margin: 0;
  overflow: hidden;
  border-bottom: solid 1vh white;
}

#menupanel h1 {
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: center;
  margin: auto;
  font-size: 5vmin;
  border: 0px;
  color: white;
}

#menuwrapper {
  margin: auto;
  text-align: center;
}

#menuwrapper table {
  width: 100%;
  font-weight: bold;
  margin-left: auto;
  margin-right: auto;
  table-layout: fixed;
  margin: 0;
  padding: 0;
  border: 0;
  margin-bottom: 10px;
}

#menuwrapper a {
  display: block;
  color: white;
  text-align: center;
  padding-left: 15%;
  padding-right: 15%;
  font-size: 1.75vmin;
  text-decoration: none;
  margin: 0;
  padding: 0;
  border: 0;
}

#menuwrapper .clickable:hover {
  -webkit-text-decoration-color: red;
          text-decoration-color: red;
}



